
.book,
.movie {
  padding-top: 10px;
}
.book section,
.movie section {
  margin-bottom: 30px;
}
.book section .title,
.movie section .title {
  margin: 0 auto 15px;
  width: 90%;
}
.book section .title h2,
.movie section .title h2 {
  overflow: hidden;
  display: inline-block;
  max-width: 550px;
  font-size: 18px;
}
.book section .title .more,
.movie section .title .more {
  float: right;
  color: #42bd56;
  font-size: 14px;
  text-decoration: none;
}
.book section .swiper-container,
.movie section .swiper-container {
  margin-left: 5%;
  width: 95%;
}
.book section .swiper-container .swiper-slide,
.movie section .swiper-container .swiper-slide {
  margin-right: 10px;
  text-align: center;
  font-size: 18px;
  width: 100px;
}
.book section .swiper-container .swiper-slide a,
.movie section .swiper-container .swiper-slide a {
  display: block;
  color: #111;
  text-decoration: none;
}
.book section .swiper-container .swiper-slide a .cover,
.movie section .swiper-container .swiper-slide a .cover {
  background-size: cover;
}
.book section .swiper-container .swiper-slide a h3,
.movie section .swiper-container .swiper-slide a h3 {
  margin-top: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 15px;
}
.book section .swiper-container .swiper-slide a .rank .star .rating-star,
.movie section .swiper-container .swiper-slide a .rank .star .rating-star {
  display: inline-block;
  margin-right: 1px;
  width: 10px;
  height: 10px;
  background-size: 10px 10px;
  background-color: transparent;
  background-repeat: no-repeat;
}
.book section .swiper-container .swiper-slide a .rank .star .rating-star.star-full,
.movie section .swiper-container .swiper-slide a .rank .star .rating-star.star-full {
  background-image: url("data:image/png;base64,");
}
.book section .swiper-container .swiper-slide a .rank .star .rating-star.star-gray,
.movie section .swiper-container .swiper-slide a .rank .star .rating-star.star-gray {
  background-image: url("data:image/png;base64,");
}
.book section .swiper-container .swiper-slide a .rank span,
.movie section .swiper-container .swiper-slide a .rank span {
  color: #aaa;
  font-size: 12px;
}
.book section .recommend,
.movie section .recommend {
  margin: 0 5%;
}
.book section .recommend a,
.movie section .recommend a {
  overflow: hidden;
  display: block;
  margin-bottom: 20px;
  color: #494949;
  text-decoration: none;
}
.book section .recommend a .cover,
.movie section .recommend a .cover {
  width: 100px;
  height: 142px;
  float: left;
  background-image: url("https://img3.doubanio.com/view/freyr_page_photo/raw/public/1611.jpg");
  margin-right: 15px;
}
.book section .recommend a .info,
.movie section .recommend a .info {
  overflow: hidden;
}
.book section .recommend a .info .title,
.movie section .recommend a .info .title {
  width: auto;
}
.book section .recommend a .info .title strong,
.movie section .recommend a .info .title strong {
  font-size: 19px;
}
.book section .recommend a .info .title em,
.movie section .recommend a .info .title em {
  float: right;
  color: #e76648;
  font-size: 16px;
  line-height: 22px;
}
.book section .recommend a .info p,
.movie section .recommend a .info p {
  color: #9b9b9b;
  font-size: 13px;
  line-height: 1.5;
}
@media screen and (max-width: 400px) {
.book section .recommend a .info p,
  .movie section .recommend a .info p {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    overflow: hidden;
}
}
@media screen and (max-width: 360px) {
.book section .recommend a .info p,
  .movie section .recommend a .info p {
    display: -webkit-box;
    overflow: hidden;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
}
}
.book section .find-book,
.movie section .find-book {
  margin-left: 5%;
}
.book section .find-book .tags-box,
.movie section .find-book .tags-box {
  width: auto;
  text-align: left;
}
.book section .find-book .tags-box ul,
.movie section .find-book .tags-box ul {
  letter-spacing: -3px;
  font-size: 0;
  white-space: nowrap;
}
.book section .find-book .tags-box ul li,
.movie section .find-book .tags-box ul li {
  display: inline-block;
  margin: 0 8px 8px 0;
  font-size: 15px;
  letter-spacing: normal;
}
.book section .find-book .tags-box ul li a,
.movie section .find-book .tags-box ul li a {
  overflow: auto;
  display: block;
  border-radius: 5px;
  border: solid 1px;
  padding: 0 20px;
  height: 50px;
  line-height: 50px;
  text-decoration: none;
}
.book section .category,
.movie section .category {
  margin-left: 5%;
}
.book section .category ul,
.movie section .category ul {
  letter-spacing: -3px;
  font-size: 0;
}
.book section .category ul li,
.movie section .category ul li {
  display: inline-block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-bottom: solid 1px #eee;
  width: 50%;
  height: 42px;
  font-size: 15px;
  letter-spacing: normal;
  vertical-align: top;
}
.book section .category ul li a,
.movie section .category ul li a {
  display: block;
  height: 42px;
  line-height: 42px;
  color: #42bd56;
  text-decoration: none;
}
.book section .category ul li a i,
.movie section .category ul li a i {
  float: right;
  margin-top: 1rem;
  border-right: solid 1px #ccc;
  border-bottom: solid 1px #ccc;
  width: 9px;
  height: 9px;
  color: #ccc;
  font-weight: bold;
  transform: rotate(-45deg);
}
.book section .category ul li:nth-child(odd) a,
.movie section .category ul li:nth-child(odd) a {
  padding-right: 7%;
}
.book section .category ul li:nth-child(even),
.movie section .category ul li:nth-child(even) {
  border-left: solid 1px #eee;
}
.book section .category ul li:nth-child(even) a,
.movie section .category ul li:nth-child(even) a {
  padding: 0 7%;
}


